<template>
	<div class="filters" :class="{ '_active': isActive }">
		<span
			class="filters__toggle-button small color-beige"
			@click="toggleFilters"
		>
			{{ isActive ? 'Скрыть фильтры' : 'Фильтры' }}
		</span>

		<div class="filters__form">
			<section v-if="type === models.NOTE">
				<Range class="mb-3" ref="range" label="Важность" @commit="setImportance" />
				<VSelect
					class="mb-3"
					placeholder="Тип"
					v-model="filters.type"
					:options="noteTypesListForSelect"
				/>
				<input type="text" placeholder="Введите текст" v-model="filters.text">
			</section>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import { mapGetters } from 'vuex';

	export default {
		name: "Filters",

		props: {
			type: {
				type: String,
				required: true,
			}
		},

		data() {
			return {
				filters: {},
				models: app.models,
				isActive: false,
			}
		},

		methods: {
			/**
			 */
			toggleFilters() {
				this.isActive = !this.isActive;
			},
			/**
			 */
			setImportance(value) {
				Vue.set(this.filters, 'importance', app.$helpers.toInt(value));
			},
		},
		
		watch: {
			/**
			 */
			filters() {
				app.commit('SET_FILTERS', { target: this.type, filters: this.filters });
			}
		},

		computed: {
			...mapGetters([
				'noteTypesListForSelect',
			])
		},
	}
</script>

<style lang="less" scoped>
	.filters {
		margin-bottom: 1rem;

		&__toggle-button {
			cursor: pointer;

			&:hover {
				text-decoration: underline;
			}
		}

		&__form {
			opacity: 0;
			max-height: 0;
			transition: max-height .2s ease-in-out;
			pointer-events: none;
		}

		&._active {
			margin-bottom: 1.5rem;
			padding-bottom: 1rem;
			border-bottom: dotted 1px white;

			.filters__form {
				opacity: 1;
				max-height: 320px;
				display: flex;
				flex-direction: column;
				pointer-events: auto;
			}
		}
	}
</style>


